
.video-js {
  width: 100% !important;
  height: 100% !important;
  position: absolute !important;
  font-size: 12px !important; 
  font-family: Helvetica, Arial, sans-serif !important;
  text-shadow: none !important;
  outline: none !important;
  border: none !important;
  overflow: hidden !important;
  transition: width 0.5s !important;
}

.shrink_video{
width: 75% !important;
}

.video-js .vjs-tech{
  outline: none !important;
}

.video-js .vjs-control-bar {
  height: 40px;
  background-color: transparent !important;
  opacity: 1;
}

.vjs-control-bar::before {
  content: '' !important;
  position: absolute !important;
  left: 0 !important;
  right: 0 !important;
  bottom: 0px !important;
  height: 120px;
  background-color: transparent !important;
  opacity: 1;
  pointer-events: none;
}



.video-js .vjs-volume-panel .vjs-volume-control.vjs-volume-vertical {
  background-color: transparent !important;
  width: 40px !important;
  height: 60px !important;
  margin: 0px !important;
  left: -40px !important;
  bottom: 60px !important;
  cursor: default !important;

  transition: visibility 0.5s, opacity 0.5s, height 0.5s 0.5s, width 0.5s 0.5s, left 0.5s 0.5s, top 0.5s 0.5s !important;
}

.video-js .vjs-volume-bar.vjs-slider-bar {
  opacity: 0.75 !important;
  height: 100% !important;
  margin-top: 0px !important;
  transition: opacity 0.1s linear !important;
}

.video-js .vjs-volume-bar.vjs-slider-bar:hover {
  opacity: 1 !important;
}

.video-js .vjs-play-control,
.video-js .vjs-mute-control,
.video-js .vjs-fullscreen-control,
.video-js .vjs-progress-control .vjs-progress-holder {
  opacity: 0.75 !important;

  transition: transform 0.1s ease-out, opacity 0.1s linear !important;
}

.video-js .vjs-play-control,
.video-js .vjs-mute-control,
.video-js .vjs-fullscreen-control {
  width: 30px !important;
  height: 30px !important;
  padding: 0px !important;
}

.video-js .vjs-icon-placeholder::before {
  content: '' !important;
}

.video-js .vjs-play-control .vjs-icon-placeholder,
.video-js .vjs-mute-control .vjs-icon-placeholder,
.video-js .vjs-fullscreen-control .vjs-icon-placeholder,
.vjs-big-play-button::after {
position: absolute !important;
left: 0px !important;
right: 0px !important;
top: 0px !important;
bottom: 0px !important;
background-image: url(data:image/svg+xml;base64,);
background-size: 120px 420px !important;
background-repeat: no-repeat !important;
}

.video-js .vjs-play-control:hover,
.video-js .vjs-mute-control:hover,
.video-js .vjs-fullscreen-control:hover,
.video-js .vjs-progress-control .vjs-progress-holder:hover {
opacity: 1 !important;
}

.video-js .vjs-play-control:active,
.video-js .vjs-mute-control:active,
.video-js .vjs-fullscreen-control:active {
transform: scale3d(0.95,0.95,1) !important;
}

.video-js .vjs-control:focus {
text-shadow: none !important;
outline: none !important;
}

.video-js .vjs-play-control.vjs-paused .vjs-icon-placeholder {
background-position: -10px -150px !important;
}

.video-js .vjs-play-control.vjs-playing .vjs-icon-placeholder {
background-position: -10px -180px !important;
}

.video-js .vjs-play-control.vjs-ended .vjs-icon-placeholder {
background-position: -10px -120px !important;
}

.video-js .vjs-mute-control .vjs-icon-placeholder {
background-position: -40px -120px !important;
}

.video-js .vjs-mute-control.vjs-vol-0 .vjs-icon-placeholder {
background-position: -40px -120px !important;
}

.video-js .vjs-mute-control.vjs-vol-1 .vjs-icon-placeholder {
background-position: -40px -150px !important;
}

.video-js .vjs-mute-control.vjs-vol-2 .vjs-icon-placeholder {
background-position: -40px -180px !important;
}

.video-js .vjs-mute-control.vjs-vol-3 .vjs-icon-placeholder {
background-position: -40px -210px !important;
}

.video-js .vjs-fullscreen-control .vjs-icon-placeholder {
background-position: -70px -150px !important;
}

.video-js.vjs-fullscreen .vjs-fullscreen-control .vjs-icon-placeholder {
background-position: -70px -180px !important;
}

.video-js .vjs-volume-panel {
width: 40px !important;
height: 40px !important;
margin: 0px !important;
}

.vjs-volume-bar.vjs-slider-vertical {
width: 4px !important;
height: 100% !important;
margin: 0px auto !important;
border-radius: 2px !important;
}

.video-js .vjs-volume-level {
position: absolute !important;
bottom: 0 !important;
left: 0 !important;
background-color: rgb(109, 20, 150) !important;
border-radius: 2px !important;
box-shadow: 0px 1px 1px rgba(0,0,0,0.25) !important;
}

.vjs-slider-vertical .vjs-volume-level::before {
  position: absolute !important;
  top: -6px !important;
  left: -4px !important;
  z-index: 1 !important;
  width: 12px !important;
  height: 12px !important;
  border-radius: 50% !important;
  background-color: #fff !important;
  content: '' !important;
  box-shadow: 0px 1px 1px rgba(0,0,0,0.25) !important;
}

.video-js .vjs-big-play-button {
display: block !important;

position: absolute !important;

width: 100px !important;
height: 100px !important;

left: 50% !important;
top: 50% !important;

margin-left: -50px !important;
margin-top: -50px !important;

background-color: transparent !important;
border: none !important;
outline: none !important;

transition: opacity 0.25s linear, transform 0.1s ease-out !important;

opacity: 0 !important;

pointer-events: none !important;
}

.vjs-big-play-button .vjs-icon-placeholder {
display: none !important;
}

.video-js.vjs-paused .vjs-big-play-button,
.video-js.vjs-ended .vjs-big-play-button {
opacity: 1 !important;

pointer-events: auto !important;
}

.vjs-big-play-button::before,
.vjs-big-play-button::after {
content: '';

position: absolute !important;

transform: translate3d(0,0,0) !important;
}

.vjs-big-play-button::before {
left: 4% !important;
right: 4% !important;
top: 4% !important;
bottom: 4% !important;

border-radius: 50% !important;

background-color: #000000 !important;

opacity: 0.45 !important;

transition: opacity 0.1s linear !important;
}

.vjs-big-play-button:hover::before {
opacity: 0.5 !important;
}

.vjs-big-play-button::after {
left: -10% !important;
right: -10% !important;
top: -10% !important;
bottom: -10% !important;

background-position: center top !important;

opacity: 0.75 !important;

transition: opacity 0.1s linear !important;
}

.video-js.vjs-ended .vjs-big-play-button::after {
background-position: center bottom !important;
}

.vjs-big-play-button:hover::after {
opacity: 1 !important;
}

.vjs-big-play-button:active {
transform: scale3d(0.95,0.95,1) !important;
}

.video-js .vjs-progress-control .vjs-progress-holder {
font-size: 1em !important;
height: 12px !important;

background-color: transparent !important;

box-shadow: 0px 1px 1px rgba(0,0,0,0.25) !important;
}

.video-js .vjs-progress-control .vjs-progress-holder::before {
content: '' !important;
position: absolute !important;
left: 0px !important;
right: 0px !important;
top: 4px !important;
height: 4px !important;
border-radius: 2px !important;
background-color: rgba(255,255,255,0.35) !important;
box-shadow: 0px 1px 1px rgba(0,0,0,0.25) !important;
}


.video-js .vjs-slider,
.video-js .vjs-progress-holder .vjs-play-progress,
.video-js .vjs-progress-holder .vjs-load-progress,
.video-js .vjs-progress-holder .vjs-load-progress div {
height: 4px !important;
margin-top: 4px !important;
border-radius: 2px !important;
background-color: rgba(255,255,255,0.25) !important;
}

.video-js .vjs-progress-holder .vjs-load-progress div {
margin-top: 0px !important;
}

.video-js .vjs-progress-holder .vjs-load-progress {
transition: width 1s ease-in-out !important;
}

.video-js .vjs-progress-holder .vjs-play-progress {
background-color: rgb(116, 24, 168) !important;
}

.video-js .vjs-play-progress::before {
content: '' !important;

position: absolute !important;

width: 12px !important;
height: 12px !important;

right: -6px !important;
top: -4px !important;

border-radius: 50% !important;
background-color: #FFFFFF !important;

box-shadow: 0px 1px 1px rgba(0,0,0,0.25) !important;
}

.video-js .vjs-time-tooltip,
.video-js .vjs-mouse-display {
display: none !important;
}

.video-js .vjs-progress-control {
cursor: default !important;
}

.video-js .vjs-current-time { display: block !important; }
.video-js .vjs-time-divider { display: block !important; }
.video-js .vjs-duration { display: block !important; }
.video-js .vjs-remaining-time { display: none !important; }

.video-js .vjs-time-control {
position: relative !important;

min-width: auto !important;

font-size: 12px !important;
line-height: 40px !important;

padding-left: 5px !important;
padding-right: 5px !important;

color: rgba(255,255,255,0.75) !important;
text-shadow: 0px 1px 1px rgba(0,0,0,0.25) !important;

user-select: none !important;
}

.video-js .vjs-current-time {
padding-left: 10px !important;
}

.video-js .vjs-icon-picture-in-picture-enter{
background-color: pink !important;
}


/*
*
*  For Video JS Menu
*
*/

.vjs-menu-button-popup .vjs-menu .vjs-menu-content{
background-color: rgba(20, 18, 20, 0.6) !important;
border: 5px solid rgba(255, 255, 255, 0.5) !important;
border-radius: 10px !important;
padding: 5px !important;
color: white !important;
font-weight: bold !important;
box-shadow: -7px -7px 20px 0 rgba(51, 49, 49, 0.4),
7px 7px 20px 0 rgba(0, 0, 0, 0.2) !important;
}

.vjs-menu-button-popup .vjs-menu li{
text-transform: uppercase !important;
font-size: 12px !important;
}

.vjs-menu-button-popup .vjs-menu li:hover{
background-color: rgba(108, 13, 163, 0.6) !important;
}

.vjs-menu-button-popup .vjs-menu li.vjs-selected{
background-color: rgba(108, 13, 163, 1) !important;
color: white !important;
}

/*
Control Buttons Images
*/

.vjs-subs-caps-button .vjs-icon-placeholder::before{
background-image: url("/closed_caption.svg") !important;
fill: white !important;
background-size: 30px 30px !important;
background-repeat: no-repeat !important;
opacity: 0.75 !important;
}

.vjs-subs-caps-button:hover{
opacity: 1 !important;
}

.vjs-audio-button button .vjs-icon-placeholder::before{
background-image: url("/audiotrack.svg") !important;
background-size: 30px 30px !important;
background-repeat: no-repeat !important;
opacity: 0.75 !important;
}

.vjs-picture-in-picture-control .vjs-icon-placeholder::before{
background-image: url("/pip.svg") !important;
background-size: 30px 30px !important;
background-repeat: no-repeat !important;
opacity: 0.75 !important;
}

.vjs-text-track-settings{
background:rgba(17, 16, 16, 0.8) !important;
backdrop-filter: blur(2px) !important;
}

.vjs-text-track-settings button{
color: red !important;
background-color: transparent !important;
border-radius: 6px !important;
border: 1px solid #dcdcdc !important;
}

/* Prevent CC settings menu item from displaying */
.vjs-texttrack-settings {
display: none !important;
}

.vjs-errors-ok-button{
border: 1px solid white !important;
border-radius: 2px !important;
}


@media(max-width:1100px){
.shrink_video{
  width: 60% !important;
}
}

@media(max-width:800px){
.shrink_video{
  width: 50% !important;
}
}

@media(max-width:650px){
.shrink_video{
  width: 0% !important;
}
}